import RestaurantSource from '../../data/restaurant-source';
import UrlParser from '../../routes/url-parser';
import { createDetailRestaurant } from '../template/template-creaor';
import LikeButtonPresenter from '../../utils/like-button-presenter';

const Detail = {
  async render() {
    return `
    <a  tabindex="1" class="skip-link-detail">Menuju ke konten</a>
    <div class="detail-container">
        <section class="detailed-restaurant">
        <div class="title-section">
          <h2>Detailed Restaurant</h2>
        </div>
        <div class="loader"></div>
        <div id="restaurant-detail"></div>

        </section>
    </div>
    <div id="likeButtonContainer"></div>
    `;
  },

  async afterRender() {
    const url = UrlParser.parseActiveUrlWithoutCombiner();
    const restaurant = await RestaurantSource.restaurantDetail(url.id);
    const detailContainer = document.querySelector('#restaurant-detail');
    const skipToContentDetail = document.querySelector('.skip-link-detail');

    skipToContentDetail.addEventListener('keypress', () => {
      document.querySelector('#restaurant-detail').scrollIntoView({ behavior: 'smooth' });
      skipToContentDetail.blur();
    });

    const loader = document.querySelector('.loader');
    const removeLoader = () => {
      loader.classList.add('hidden');
    };

    removeLoader();
    detailContainer.innerHTML = createDetailRestaurant(restaurant);
    LikeButtonPresenter.init({
      likeButtonContainer: document.querySelector('.like-button-container'),
      restaurant: {
        id: restaurant.id,
        name: restaurant.name,
        pictureId: restaurant.pictureId,
        city: restaurant.city,
        rating: restaurant.rating,
      },
    });
  },
};

export default Detail;
